
h1[data-v-1] {
  color: #42b983;
}
.red[data-v-1] {
  color: red;
}
.list[data-v-1] {
  width: 50%;
  font-size: 1rem;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
}
.list .item[data-v-1]:not(.active) {
  color: gray;
}
.list .item:not(.active) a[data-v-1] {
  color: #aca !important;
}
.list .item.active a[data-v-1] {
  text-decoration: underline !important;
}
.list .item input[data-v-1]{
  margin-right: 5px;
}



